@import "reset";
@import "vars";
html,
body {
    width: 100%;
    height: 100%;
}

.wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    font-family: $font-primary;
    font-size: 14px;
    box-sizing: border-box;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center;
}

.container {
    padding: 15px 24px 15px 24px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 520px;
    background-color: #ffffff;
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 0 0 20px 0 rgba(2, 3, 3, 0.05);
    border-radius: 10px;
    @media only screen and (max-width: 550px) {
        padding-left: 5px;
        padding-right: 5px;
    }
}

// HEADER
.header__logo {
    margin-top: 15px;
    max-width: 166px;
}

// MAIN CONTENT
.main {
    &__title {
        position: relative;
        margin-top: 43px;
        margin-bottom: 36px;
        font-size: 22.5px;
        text-align: center;
        text-transform: uppercase;
        font-weight: bold;
        color: #2e2e2e;
        &:after {
            content: '';
            position: absolute;
            top: 0;
            bottom: -40px;
            left: 0;
            right: 0;
            margin: auto;
            max-width: 183px;
            width: 51.5%;
            height: 2.25px;
            background-color: #f47922;
        }
    }
    &__text {
        margin-bottom: 37.5px;
        font-family: Roboto;
        font-size: 13.5px;
        line-height: 1.6;
        text-align: center;
        color: #2e2e2e;
    }
    &__form {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding-left: 27px;
        padding-right: 27px;
        font-size: 14px;
        @media only screen and (max-width: 550px) {
            padding: 0;
        }
    }
    input,
    .form__notify {
        height: 35px;
        font-size: 13.5px;
        box-sizing: border-box;
        border: solid 1.5px #f47922;
        &:focus {
            outline: none;
            border-color: #8cc63f;
        }
        @media only screen and (max-width: 450px) {
            width: 100%;
            margin-top: 10px;
            border-radius: 3px;
        }
    }
    input {
        font-size: 12px;
        &::-webkit-input-placeholder {
            font-family: Raleway;
            font-weight: 500;
            color: #2e2e2e;
        }
        &::-moz-placeholder {
            font-family: Raleway;
            font-weight: 500;
            color: #2e2e2e;
        }
        &:-ms-input-placeholder {
            color: #333!important;
        }
        &::placeholder {
            font-family: Raleway;
            font-weight: 500;
            color: #2e2e2e;
        }
    }
    .form__email {
        flex-grow: 1;
        margin-right: 0;
        padding-left: 13px;
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
    }
    .form__notify {
        margin-left: -2px;
        padding-left: 31.5px;
        padding-right: 31.5px;
        font-family: Roboto;
        font-weight: 500;
        text-align: center;
        color: #ffffff;
        background-color: #f47922;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
        transition: all 0.2s ease;
        &:hover {
            cursor: pointer;
            background-color: #8cc63f;
            border-color: #8cc63f;
        }
    }
}

.footer {
    width: 100%;
    margin-top: 29.25px;
    padding-top: 17.25px;
    display: flex;
    justify-content: flex-end;
    border-top: 1px solid #c5c5c5;
    &__follow {
        margin-right: 7px;
        padding-right: 6px;
        border-right: 1px solid #5f5f5f;
        font-size: 12px;
        font-family: Raleway;
        text-transform: uppercase;
        font-weight: 500;
        color: #5f5f5f;
    }
    .icon {
        margin-right: 7px;
        font-size: 14px;
        color: #2e2e2e;
        transition: all 0.2s ease;
        &:hover {
            color: #8cc63f;
        }
    }
}

@import "media";